.contacts {
  display: flex;
  justify-content: space-between;
}

.contacts__text {
  margin: 0;
  padding-right: 10px;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  word-break: break-word;
  width: 140px;
}

.contacts__address {
  margin: 0;
  word-break: break-word;
  width: 140px;
}
